.statistics {
  width: 400px;
  background-color: rgb(182, 247, 233);
}

.title {
  font-size: 40px;
  display: flex;
  justify-content: center;
}

.list {
  border: 1px solid black;
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0px;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.label {
  color: aliceblue;
  font-weight: 900;
  margin-bottom: 5px;
}

.percentage {
  color: aliceblue;
}
